<template>
	<view class="page">
		<view class="title">新增记录</view>
		<view class="form">
			<picker :range="types" @change="onTypeChange">
				<view class="picker">类型：{{ types[typeIndex] }}</view>
			</picker>
			<input class="input" placeholder="描述（如：鸡胸+猫粮 50g）" v-model="desc" />
			<button type="primary" class="submit" @tap="submit">保存</button>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			types: ['喂食','饮水','清洁','体重','用药','疫苗','日记','照片'],
			typeIndex: 0,
			desc: ''
		}
	},
	methods: {
		onTypeChange(e) {
			this.typeIndex = Number(e.detail.value || 0)
		},
		submit() {
			uni.showToast({ title: '已保存', icon: 'success' })
			setTimeout(() => {
				uni.navigateBack()
			}, 300)
		}
	}
}
</script>

<style scoped>
.page { padding: 24rpx; }
.title { font-size: 36rpx; font-weight: 600; margin-bottom: 24rpx; }
.picker { padding: 20rpx; background: #fff; border-radius: 12rpx; margin-bottom: 16rpx; }
.input { background: #fff; border-radius: 12rpx; padding: 20rpx; margin-bottom: 24rpx; }
.submit { background: #ffd54f; color: #333; }
</style>
